import { api } from './authService';

// Leave Request API endpoints
const leaveService = {
  // Get leave requests with filtering
  getLeaveRequests: async (params = {}) => {
    const queryParams = new URLSearchParams();

    Object.keys(params).forEach(key => {
      if (params[key] !== undefined && params[key] !== null) {
        queryParams.append(key, params[key]);
      }
    });

    const response = await api.get(`/leaves?${queryParams.toString()}`);
    return response.data;
  },

  // Get leave request by ID
  getLeaveRequestById: async (id) => {
    const response = await api.get(`/leaves/${id}`);
    return response.data;
  },

  // Create new leave request
  createLeaveRequest: async (requestData) => {
    const response = await api.post('/leaves', requestData);
    return response.data;
  },

  // Update leave request
  updateLeaveRequest: async (id, updateData) => {
    const response = await api.put(`/leaves/${id}`, updateData);
    return response.data;
  },

  // Cancel leave request
  cancelLeaveRequest: async (id) => {
    const response = await api.put(`/leaves/${id}/cancel`);
    return response.data;
  },

  // Get leave balances
  getLeaveBalances: async (year) => {
    const response = await api.get(`/leaves/balances?year=${year || new Date().getFullYear()}`);
    return response.data;
  },

  // Get available leave types
  getLeaveTypes: async () => {
    const response = await api.get('/leaves/types');
    return response.data;
  },

  // Approve leave request (Manager)
  approveByManager: async (id, comments) => {
    const response = await api.put(`/leaves/${id}/approve/manager`, { comments });
    return response;
  },

  // Reject leave request (Manager)
  rejectByManager: async (id, comments) => {
    const response = await api.put(`/leaves/${id}/reject/manager`, { comments });
    return response;
  },

  // Approve leave request (Admin)
  approveByAdmin: async (id, comments) => {
    const response = await api.put(`/leaves/${id}/approve/admin`, { comments });
    return response;
  },

  // Reject leave request (Admin)
  rejectByAdmin: async (id, comments) => {
    const response = await api.put(`/leaves/${id}/reject/admin`, { comments });
    return response;
  },

  // Get pending approvals
  getPendingApprovals: async (type) => {
    const response = await api.get(`/leaves/pending-approvals?type=${type}`);
    return response;
  },

  // Get leave statistics
  getLeaveStatistics: async (year) => {
    const response = await api.get(`/leaves/statistics?year=${year || new Date().getFullYear()}`);
    return response;
  },

  // Validate leave request (client-side validation too)
  validateLeaveRequest: async (requestData) => {
    try {
      // This would validate against backend rules
      const response = await api.post('/leaves/validate', requestData);
      return response.data;
    } catch (error) {
      if (error.response?.data?.message) {
        throw new Error(error.response.data.message);
      }
      throw error;
    }
  }
};

export default leaveService;