import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  fetchLeaveRequests,
  selectLeaveRequests,
  selectLeaveLoading,
  selectLeaveError,
  selectLeavePagination
} from '../store/slices/leaveSlice';
import { selectCurrentUser } from '../store/slices/authSlice';
import {
  Calendar,
  Clock,
  CheckCircle,
  XCircle,
  Eye,
  Plus,
  Filter,
  Search,
  ChevronLeft,
  ChevronRight
} from 'lucide-react';

const LeaveRequests = () => {
  const dispatch = useDispatch();
  const currentUser = useSelector(selectCurrentUser);
  const requests = useSelector(selectLeaveRequests);
  const isLoading = useSelector(selectLeaveLoading);
  const error = useSelector(selectLeaveError);
  const pagination = useSelector(selectLeavePagination);

  const [filters, setFilters] = useState({
    status: '',
    leave_type: '',
    search: '',
    start_date: '',
    end_date: ''
  });

  const [showFilters, setShowFilters] = useState(false);

  useEffect(() => {
    loadRequests(1);
  }, [filters]);

  const loadRequests = (page = 1) => {
    const params = { page, ...filters };
    // Remove empty filters
    Object.keys(params).forEach(key => {
      if (!params[key]) delete params[key];
    });
    dispatch(fetchLeaveRequests(params));
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'pending':
        return 'bg-yellow-100 text-yellow-800';
      case 'manager_approved':
        return 'bg-blue-100 text-blue-800';
      case 'admin_approved':
        return 'bg-green-100 text-green-800';
      case 'manager_rejected':
      case 'admin_rejected':
        return 'bg-red-100 text-red-800';
      case 'cancelled':
        return 'bg-gray-100 text-gray-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'pending':
        return <Clock className="w-4 h-4" />;
      case 'manager_approved':
      case 'admin_approved':
        return <CheckCircle className="w-4 h-4" />;
      case 'manager_rejected':
      case 'admin_rejected':
      case 'cancelled':
        return <XCircle className="w-4 h-4" />;
      default:
        return null;
    }
  };

  const getLeaveTypeColor = (leaveType) => {
    switch (leaveType?.toLowerCase()) {
      case 'annual':
        return 'text-green-600';
      case 'sick':
        return 'text-red-600';
      case 'personal':
        return 'text-blue-600';
      default:
        return 'text-gray-600';
    }
  };

  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const clearFilters = () => {
    setFilters({
      status: '',
      leave_type: '',
      search: '',
      start_date: '',
      end_date: ''
    });
  };

  const formatStatus = (status) => {
    return status
      .replace('_', ' ')
      .replace(/\b\w/g, l => l.toUpperCase());
  };

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      {/* Header */}
      <div className="bg-white shadow rounded-lg p-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-gray-900">My Leave Requests</h1>
            <p className="text-gray-600 mt-1">View and manage your leave requests</p>
          </div>
          <button
            onClick={() => window.location.href = '/leave-request'}
            className="inline-flex items-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            <Plus className="w-4 h-4 mr-2" />
            New Request
          </button>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white shadow rounded-lg">
        <div className="px-6 py-4 border-b border-gray-200">
          <button
            onClick={() => setShowFilters(!showFilters)}
            className="inline-flex items-center px-3 py-2 border border-gray-300 rounded-md text-sm font-medium text-gray-700 bg-white hover:bg-gray-50"
          >
            <Filter className="w-4 h-4 mr-2" />
            Filters
          </button>
        </div>

        {showFilters && (
          <div className="px-6 py-4 border-b border-gray-200">
            <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Status
                </label>
                <select
                  name="status"
                  value={filters.status}
                  onChange={handleFilterChange}
                  className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                >
                  <option value="">All Statuses</option>
                  <option value="pending">Pending</option>
                  <option value="manager_approved">Manager Approved</option>
                  <option value="admin_approved">Admin Approved</option>
                  <option value="manager_rejected">Manager Rejected</option>
                  <option value="admin_rejected">Admin Rejected</option>
                  <option value="cancelled">Cancelled</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Leave Type
                </label>
                <select
                  name="leave_type"
                  value={filters.leave_type}
                  onChange={handleFilterChange}
                  className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                >
                  <option value="">All Types</option>
                  <option value="annual">Annual</option>
                  <option value="sick">Sick</option>
                  <option value="personal">Personal</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  From Date
                </label>
                <input
                  type="date"
                  name="start_date"
                  value={filters.start_date}
                  onChange={handleFilterChange}
                  className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  To Date
                </label>
                <input
                  type="date"
                  name="end_date"
                  value={filters.end_date}
                  onChange={handleFilterChange}
                  className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                />
              </div>

              <div className="flex items-end">
                <button
                  onClick={clearFilters}
                  className="px-3 py-2 border border-gray-300 rounded-md text-sm font-medium text-gray-700 bg-white hover:bg-gray-50"
                >
                  Clear
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Results */}
        <div className="px-6 py-4">
          {isLoading ? (
            <div className="flex items-center justify-center py-12">
              <div className="text-center">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto"></div>
                <p className="mt-4 text-gray-600">Loading requests...</p>
              </div>
            </div>
          ) : error ? (
            <div className="text-center py-12">
              <div className="text-red-600">
                <p className="text-lg font-medium">Error loading requests</p>
                <p className="text-sm mt-1">{error}</p>
              </div>
            </div>
          ) : requests.length === 0 ? (
            <div className="text-center py-12">
              <Calendar className="mx-auto h-12 w-12 text-gray-400" />
              <h3 className="mt-2 text-sm font-medium text-gray-900">No leave requests found</h3>
              <p className="mt-1 text-sm text-gray-500">
                You haven't submitted any leave requests yet.
              </p>
              <div className="mt-6">
                <button
                  onClick={() => window.location.href = '/leave-request'}
                  className="inline-flex items-center px-4 py-2 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700"
                >
                  <Plus className="w-4 h-4 mr-2" />
                  Submit Your First Request
                </button>
              </div>
            </div>
          ) : (
            <>
              <div className="mb-4 text-sm text-gray-700">
                Showing {requests.length} request{requests.length !== 1 ? 's' : ''}
              </div>

              <div className="space-y-3">
                {requests.map((request) => (
                  <div
                    key={request.id}
                    className="bg-gray-50 rounded-lg p-4 hover:bg-gray-100 transition-colors"
                  >
                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-4">
                        <div className={`flex items-center space-x-1 px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(request.status)}`}>
                          {getStatusIcon(request.status)}
                          <span>{formatStatus(request.status)}</span>
                        </div>
                        <div>
                          <div className="flex items-center space-x-2">
                            <Calendar className={`w-4 h-4 ${getLeaveTypeColor(request.leaveType)}`} />
                            <span className="font-medium text-gray-900 capitalize">
                              {request.leaveType} Leave
                            </span>
                          </div>
                          <div className="text-sm text-gray-500 mt-1">
                            {request.dates?.start && request.dates?.end ? (
                              <>
                                {new Date(request.dates.start).toLocaleDateString()} - {new Date(request.dates.end).toLocaleDateString()}
                                <span className="ml-2">({request.dates.totalDays} days)</span>
                              </>
                            ) : (
                              <>
                                {new Date(request.start_date).toLocaleDateString()} - {new Date(request.end_date).toLocaleDateString()}
                                <span className="ml-2">({request.total_days} days)</span>
                              </>
                            )}
                          </div>
                        </div>
                      </div>

                      <div className="flex items-center space-x-3">
                        <span className="text-sm text-gray-500">
                          {new Date(request.created_at).toLocaleDateString()}
                        </span>

                        <button
                          onClick={() => window.location.href = `/leave-request/${request.id}`}
                          className="inline-flex items-center px-3 py-1 border border-gray-300 rounded-md text-sm font-medium text-gray-700 bg-white hover:bg-gray-50"
                        >
                          <Eye className="w-4 h-4 mr-1" />
                          View
                        </button>
                      </div>
                    </div>

                    {request.reason && (
                      <div className="mt-3 text-sm text-gray-600">
                        <span className="font-medium">Reason:</span> {request.reason}
                      </div>
                    )}
                  </div>
                ))}
              </div>

              {/* Pagination */}
              {pagination.total_pages > 1 && (
                <div className="mt-6 flex items-center justify-between">
                  <div className="text-sm text-gray-700">
                    Page {pagination.current_page} of {pagination.total_pages}
                    ({pagination.total_requests} total requests)
                  </div>

                  <div className="flex items-center space-x-2">
                    <button
                      onClick={() => loadRequests(pagination.current_page - 1)}
                      disabled={!pagination.has_prev}
                      className={`px-3 py-1 text-sm font-medium rounded-md ${
                        pagination.has_prev
                          ? 'text-gray-700 bg-white border border-gray-300 hover:bg-gray-50'
                          : 'text-gray-400 bg-gray-100 cursor-not-allowed'
                      }`}
                    >
                      <ChevronLeft className="w-4 h-4 mr-1" />
                      Previous
                    </button>

                    <button
                      onClick={() => loadRequests(pagination.current_page + 1)}
                      disabled={!pagination.has_next}
                      className={`px-3 py-1 text-sm font-medium rounded-md ${
                        pagination.has_next
                          ? 'text-gray-700 bg-white border border-gray-300 hover:bg-gray-50'
                          : 'text-gray-400 bg-gray-100 cursor-not-allowed'
                      }`}
                    >
                      Next
                      <ChevronRight className="w-4 h-4 ml-1" />
                    </button>
                  </div>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default LeaveRequests;